import WebApi from "api/WebApi";
export function CreatePost(payload) {
    return WebApi.Post("posts", payload);
}
export function UpdatePost(payload) {
    return WebApi.Put(`posts/${payload.id}`, payload);
}
export function FetchTopicPosts(payload) {
    return WebApi.Get(`posts`, payload);
}
export default {
    CreatePost,
    UpdatePost,
    FetchTopicPosts
};



